import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { declinationOfNumerals } from '../../utils/utils';

import './style-advantages.css';
import './style-services.css';

class Home extends Component {
  constructor(props) {
    super(props);
    this.findExecutedJobs = this.findExecutedJobs.bind(this);
    this.numberOfJobs = this.numberOfJobs.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  findExecutedJobs(categoryString) {
    const { jobs } = this.props;
    return jobs.filter(job => job.category === categoryString);
  }

  numberOfJobs(categoryString) {
    const { isReady } = this.props;
    return isReady ? this.findExecutedJobs(categoryString).length : <span>&infin;</span>;
  }

  render() {
    const arrayJobsString = ['', '', ''];

    return (
      <main>
        <section className="advantages">
          <h2 className="visually-hidden">   ,   </h2>
          <div className="advantages__wrapper">
            <ul className="advantages__list">
              <li className="advantages__item">
                <h3 className="advantages__item-title"> </h3>
                <p className="advantages__item-paragraph"><span className="advantages__item-paragraph--marker"></span> <span className="advantages__item-paragraph--marker"></span>  <span className="advantages__item-paragraph--marker"></span>,     </p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title"> </h3>
                <p className="advantages__item-paragraph"> <span className="advantages__item-paragraph--marker"></span> <span className="advantages__item-paragraph--marker"></span>    <span className="advantages__item-paragraph--marker"></span> <span className="advantages__item-paragraph--marker"></span>,    </p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title"> </h3>
                <p className="advantages__item-paragraph"> <span className="advantages__item-paragraph--marker"></span> <span className="advantages__item-paragraph--marker"></span>  <span className="advantages__item-paragraph--marker"></span>     </p>
              </li>
              <li className="advantages__item">
                <h3 className="advantages__item-title"> </h3>
                <p className="advantages__item-paragraph"><span className="advantages__item-paragraph--marker"></span>    , <span className="advantages__item-paragraph--marker"></span>  <span className="advantages__item-paragraph--marker"></span>  </p>
              </li>
            </ul>
          </div>
        </section>
        <section className="services  services--dark">
          <div className="services__wrapper">
            <h2 className="services__title  services__title--dark"><span></span><br/><br/></h2>
            <div className="services__wrapper-one">
              <p className="services__number  services__number--dark">01</p>
              <p className="services__number-big">01</p>
              <p className="services__jobs  services__jobs--dark"><span className="services__jobs-numbers  services__jobs-numbers--dark  services__jobs-numbers--manicure">{ this.numberOfJobs('manicure') }</span> { declinationOfNumerals(this.numberOfJobs('manicure'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two  services__title-two--dark" id="manicure"></h3>
            <p className="services__title-free  services__title-free--dark"></p>
            <p className="services__description  services__description--dark">    ,   ,    ,  ,   , , ,    .         ,      .</p>
            <div className="services__loader">
              <p className="services__loader-text">...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt=""/>
            </div>
            
            
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/manicure"> </Link>
              <Link className="button  services__price-btn  services__price-btn--dark" to="/prices"></Link>
            </div>
          </div>
        </section>
        <section className="services">
          <div className="services__wrapper">
            <h2 className="services__title"><span></span><br/><br/></h2>
            <div className="services__wrapper-one">
              <p className="services__number">02</p>
              <p className="services__number-big">02</p>
              <p className="services__jobs"><span className="services__jobs-numbers  services__jobs-numbers--lashes">{ this.numberOfJobs('lashes') }</span> { declinationOfNumerals(this.numberOfJobs('lashes'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two" id="lashes"></h3>
            <p className="services__title-free"></p>
            <p className="services__description">       ,       .       ,         .        .      .</p>
            <div className="services__loader">
              <p className="services__loader-text  services__loader-text--dark">...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt=""/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                3
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                4
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                5
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                6
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                7
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/lashes"> </Link>
              <Link className="button  services__price-btn" to="/prices"></Link>
            </div>
          </div>
        </section>
        <section className="services  services--dark">
          <div className="services__wrapper">
            <h2 className="services__title  services__title--dark"><span></span><br/></h2>
            <div className="services__wrapper-one">
              <p className="services__number  services__number--dark">03</p>
              <p className="services__number-big">03</p>
              <p className="services__jobs  services__jobs--dark"><span className="services__jobs-numbers  services__jobs-numbers--dark  services__jobs-numbers--brow">{ this.numberOfJobs('brow') }</span> { declinationOfNumerals(this.numberOfJobs('brow'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two  services__title-two--dark" id="brow"></h3>
            <p className="services__title-free  services__title-free--dark"> </p>
            <p className="services__description  services__description--dark">      ,              ,       .     .      ,    ,    .    ,        .</p>
            <div className="services__loader">
              <p className="services__loader-text">...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt=""/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                3
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                4
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/brow"> </Link>
              <Link className="button  services__price-btn  services__price-btn--dark" to="/prices"></Link>
            </div>
          </div>
        </section>
        <section className="services">
          <div className="services__wrapper">
            <h2 className="services__title"><span></span><br/><br/></h2>
            <div className="services__wrapper-one">
              <p className="services__number">04</p>
              <p className="services__number-big">04</p>
              <p className="services__jobs"><span className="services__jobs-numbers  services__jobs-numbers--cosmet">{ this.numberOfJobs('cosmet') }</span> { declinationOfNumerals(this.numberOfJobs('brow'), arrayJobsString) }</p>
            </div>
            <h3 className="services__title-two" id="cosmetology"></h3>
            <p className="services__title-free"></p>
            <p className="services__description  services__description--cosmet">          .          .    ,        ,  :  ,   , ,   , , ,   ,   .</p>
            <div className="services__loader">
              <p className="services__loader-text  services__loader-text--dark">...</p>
              <img src="/img/spinner.gif" width="70" height="70" alt=""/>
            </div>
            <ul className="services__slider  hidden">
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                1
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                2
              </li>
              <li className="services__slide">
                <img src="" width="" height="" alt=" "/>
                3
              </li>
            </ul>
            <div className="services__wrapper-two">
              <Link className="button  services__jobs-btn" to="/jobs/cosmetologist"> </Link>
              <Link className="button  services__price-btn" to="/prices"></Link>
            </div>
          </div>
        </section>
      </main>
    );
  }
}

export default Home;


